import {
  Directive,
  ElementRef,
  Input,
  DoCheck,
  OnDestroy,
  Renderer,
  Renderer2,
  IterableDiffers,
  KeyValueDiffers, SimpleChanges, OnChanges
} from '@angular/core';
import {NgClass} from '@angular/common';

import {BaseFxDirectiveAdapter} from './base-adapter';
import {MediaChange} from '../../media-query/media-change';
import {MediaMonitor} from '../../media-query/media-monitor';

/** NgClass allowed inputs **/
export type NgClassType = string | string[] | Set<string> | {[klass: string]: any};

/**
 * Directive to add responsive support for ngClass.
 */

export class ClassDirective extends NgClass implements DoCheck, OnChanges, OnDestroy {

  /**
   * Intercept ngClass assignments so we cache the default classes
   * which are merged with activated styles or used as fallbacks.
   * Note: Base ngClass values are applied during ngDoCheck()
   */
  
  set ngClassBase(val: NgClassType) {
    this._ngClassAdapter.cacheInput('ngClass', val, true);
    this.ngClass = val;
  }

  /* tslint:disable */
      set ngClassXs(val:   NgClassType) { this._ngClassAdapter.cacheInput('ngClassXs',   val, true); }
      set ngClassSm(val:   NgClassType) { this._ngClassAdapter.cacheInput('ngClassSm',   val, true); }
      set ngClassMd(val:   NgClassType) { this._ngClassAdapter.cacheInput('ngClassMd',   val, true); }
      set ngClassLg(val:   NgClassType) { this._ngClassAdapter.cacheInput('ngClassLg',   val, true); }
      set ngClassXl(val:   NgClassType) { this._ngClassAdapter.cacheInput('ngClassXl',   val, true); }

   set ngClassLtSm(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassLtSm', val, true); }
   set ngClassLtMd(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassLtMd', val, true); }
   set ngClassLtLg(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassLtLg', val, true); }
   set ngClassLtXl(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassLtXl', val, true); }

   set ngClassGtXs(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassGtXs', val, true); }
   set ngClassGtSm(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassGtSm', val, true); }
   set ngClassGtMd(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassGtMd', val, true); }
   set ngClassGtLg(val: NgClassType) { this._ngClassAdapter.cacheInput('ngClassGtLg', val, true); }

  /** Deprecated selectors */

  /**
   * Base class selector values get applied immediately and are considered destructive overwrites to
   * all previous class assignments
   *
   * Delegate to NgClass:klass setter and cache value for base fallback from responsive APIs.
   */
  
  set classBase(val: string) {
    this._classAdapter.cacheInput('_rawClass', val, true);
    this.klass = val;
  }

        set classXs(val:   NgClassType) { this._classAdapter.cacheInput('classXs',   val, true); }
        set classSm(val:   NgClassType) { this._classAdapter.cacheInput('classSm',   val, true); }
        set classMd(val:   NgClassType) { this._classAdapter.cacheInput('classMd',   val, true); }
        set classLg(val:   NgClassType) { this._classAdapter.cacheInput('classLg',   val, true); }
        set classXl(val:   NgClassType) { this._classAdapter.cacheInput('classXl',   val, true); }

     set classLtSm(val: NgClassType) { this._classAdapter.cacheInput('classLtSm', val, true); }
     set classLtMd(val: NgClassType) { this._classAdapter.cacheInput('classLtMd', val, true); }
     set classLtLg(val: NgClassType) { this._classAdapter.cacheInput('classLtLg', val, true); }
     set classLtXl(val: NgClassType) { this._classAdapter.cacheInput('classLtXl', val, true); }

     set classGtXs(val: NgClassType) { this._classAdapter.cacheInput('classGtXs', val, true); }
     set classGtSm(val: NgClassType) { this._classAdapter.cacheInput('classGtSm', val, true); }
     set classGtMd(val: NgClassType) { this._classAdapter.cacheInput('classGtMd', val, true); }
     set classGtLg(val: NgClassType) { this._classAdapter.cacheInput('classGtLg', val, true); }

  /**
   * Initial value of the `class` attribute; used as
   * fallback and will be merged with nay `ngClass` values
   */
  get initialClasses() : string {
    return  this._classAdapter.queryInput('_rawClass') || "";
  }

  /* tslint:enable */
  constructor(protected monitor: MediaMonitor,
              _iterableDiffers: IterableDiffers, _keyValueDiffers: KeyValueDiffers,
              _ngEl: ElementRef, _oldRenderer: Renderer, _renderer: Renderer2) {

    // TODO: this should use Renderer2 as well, but NgClass hasn't switched over yet.
    super(_iterableDiffers, _keyValueDiffers, _ngEl, _oldRenderer);

    this._classAdapter = new BaseFxDirectiveAdapter('class', monitor, _ngEl, _renderer);
    this._ngClassAdapter = new BaseFxDirectiveAdapter('ngClass', monitor, _ngEl, _renderer);
  }

  // ******************************************************************
  // Lifecycle Hooks
  // ******************************************************************

  /**
   * For @Input changes on the current mq activation property
   */
  ngOnChanges(changes: SimpleChanges) {
    if (this._classAdapter.activeKey in changes) {
      this._updateKlass();
    }
    if (this._ngClassAdapter.activeKey in changes) {
      this._updateNgClass();
    }
  }

  /**
   * For ChangeDetectionStrategy.onPush and ngOnChanges() updates
   */
  ngDoCheck() {
    if (!this._classAdapter.hasMediaQueryListener) {
      this._configureMQListener();
    }
    super.ngDoCheck();
  }

  ngOnDestroy() {
    this._classAdapter.ngOnDestroy();
    this._ngClassAdapter.ngOnDestroy();
  }

  // ******************************************************************
  // Internal Methods
  // ******************************************************************

  /**
   * Build an mqActivation object that bridges
   * mql change events to onMediaQueryChange handlers
   */
  protected _configureMQListener() {
    this._classAdapter.listenForMediaQueryChanges('class', '', (changes: MediaChange) => {
      this._updateKlass(changes.value);
    });

    this._ngClassAdapter.listenForMediaQueryChanges('ngClass', '', (changes: MediaChange) => {
      this._updateNgClass(changes.value);
      super.ngDoCheck();    // trigger NgClass::_applyIterableChanges()
    });
  }

  /**
   *  Apply updates directly to the NgClass:klass property
   *  ::ngDoCheck() is not needed
   */
  protected _updateKlass(value?: NgClassType) {
    let klass = value || this._classAdapter.queryInput('class') || '';
    if (this._classAdapter.mqActivation) {
      klass = this._classAdapter.mqActivation.activatedInput;
    }
    this.klass = klass || this.initialClasses;
  }

  /**
   *  Identify the activated input value and update the ngClass iterables...
   *  needs ngDoCheck() to actually apply the values to the element
   */
  protected _updateNgClass(value?: NgClassType) {
    if (this._ngClassAdapter.mqActivation) {
      value = this._ngClassAdapter.mqActivation.activatedInput;
    }
    this.ngClass = value || '';     // Delegate subsequent activity to the NgClass logic
  }

  /**
   * Special adapter to cross-cut responsive behaviors
   * into the ClassDirective
   */
  protected _classAdapter: BaseFxDirectiveAdapter;   // used for `class.xxx` selectores
  protected _ngClassAdapter: BaseFxDirectiveAdapter;   // used for `ngClass.xxx` selectors
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: `
    [class], [class.xs], [class.sm], [class.md], [class.lg], [class.xl],
    [class.lt-sm], [class.lt-md], [class.lt-lg], [class.lt-xl],
    [class.gt-xs], [class.gt-sm], [class.gt-md], [class.gt-lg],

    [ngClass], [ngClass.xs], [ngClass.sm], [ngClass.md], [ngClass.lg], [ngClass.xl],
    [ngClass.lt-sm], [ngClass.lt-md], [ngClass.lt-lg], [ngClass.lt-xl],
    [ngClass.gt-xs], [ngClass.gt-sm], [ngClass.gt-md], [ngClass.gt-lg]
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: MediaMonitor, },
{type: IterableDiffers, },
{type: KeyValueDiffers, },
{type: ElementRef, },
{type: Renderer, },
{type: Renderer2, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'ngClassBase': [{ type: Input, args: ['ngClass', ] },],
'ngClassXs': [{ type: Input, args: ['ngClass.xs', ] },],
'ngClassSm': [{ type: Input, args: ['ngClass.sm', ] },],
'ngClassMd': [{ type: Input, args: ['ngClass.md', ] },],
'ngClassLg': [{ type: Input, args: ['ngClass.lg', ] },],
'ngClassXl': [{ type: Input, args: ['ngClass.xl', ] },],
'ngClassLtSm': [{ type: Input, args: ['ngClass.lt-sm', ] },],
'ngClassLtMd': [{ type: Input, args: ['ngClass.lt-md', ] },],
'ngClassLtLg': [{ type: Input, args: ['ngClass.lt-lg', ] },],
'ngClassLtXl': [{ type: Input, args: ['ngClass.lt-xl', ] },],
'ngClassGtXs': [{ type: Input, args: ['ngClass.gt-xs', ] },],
'ngClassGtSm': [{ type: Input, args: ['ngClass.gt-sm', ] },],
'ngClassGtMd': [{ type: Input, args: ['ngClass.gt-md', ] },],
'ngClassGtLg': [{ type: Input, args: ['ngClass.gt-lg', ] },],
'classBase': [{ type: Input, args: ['class', ] },],
'classXs': [{ type: Input, args: ['class.xs', ] },],
'classSm': [{ type: Input, args: ['class.sm', ] },],
'classMd': [{ type: Input, args: ['class.md', ] },],
'classLg': [{ type: Input, args: ['class.lg', ] },],
'classXl': [{ type: Input, args: ['class.xl', ] },],
'classLtSm': [{ type: Input, args: ['class.lt-sm', ] },],
'classLtMd': [{ type: Input, args: ['class.lt-md', ] },],
'classLtLg': [{ type: Input, args: ['class.lt-lg', ] },],
'classLtXl': [{ type: Input, args: ['class.lt-xl', ] },],
'classGtXs': [{ type: Input, args: ['class.gt-xs', ] },],
'classGtSm': [{ type: Input, args: ['class.gt-sm', ] },],
'classGtMd': [{ type: Input, args: ['class.gt-md', ] },],
'classGtLg': [{ type: Input, args: ['class.gt-lg', ] },],
};
}


interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
